import { LayoutProvider } from '@/components/providers/layout-provider'
import { Toaster } from 'sonner'
import './globals.css'

export default function RootLayout({
  children,
}: {
  children: React.ReactNode
}) {
  return (
    <html lang="en">
      <body>
        <LayoutProvider>
          {children}
          <Toaster
            position="top-right" // Position des Toasts
            expand={false} // Animation
            richColors // Reichere Farben
            closeButton // SchlieÃŸen-Button anzeigen
            theme="system" // 'light', 'dark', oder 'system'
          />
        </LayoutProvider>
      </body>
    </html>
  )
}